@import "https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  min-height: 100vh;
  background: #11131e;
  justify-content: center;
  align-items: center;
  display: flex;
}

.sortable-list {
  width: 425px;
  background: #fff;
  border-radius: 7px;
  padding: 30px 25px 20px;
  box-shadow: 0 15px 30px #0000001a;
}

.sortable-list .item {
  cursor: move;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 11px;
  padding: 10px 13px;
  list-style: none;
  display: flex;
}

.item .details {
  align-items: center;
  display: flex;
}

.item .details img {
  height: 43px;
  width: 43px;
  pointer-events: none;
  object-fit: cover;
  border-radius: 50%;
  margin-right: 12px;
}

.item .details span {
  font-size: 1.13rem;
}

.item i {
  color: #474747;
  font-size: 1.13rem;
}

.item.dragging {
  opacity: .6;
}

.item.dragging :where(.details, i) {
  opacity: 0;
}

